import Checked from '@/static/img/icon-checked.png'
import Uncheck from '@/static/img/icon-uncheck.png'
import {
	getCollectGoodsList,
	getCollectGoodsRemove
} from '@/api/userApi.js'
import T from '@/utils/tips.js'
import Dialog from '@/components/common/Dialog.vue'
import NavigationBar from '@/components/common/NavigationBar.vue'
export default {
	data() {
		return {
			currentPage: 1,
			list: [],
			isEdit: false,
			countChecked: 0 /* 选中的计数器 */ ,
			total: 0,
			Checked,
			Uncheck,
			pageSize: 10,
			allLoaded: false,
			loadText: '上拉加载更多...',
			hasAllCheck: false,
			ids: [],
			isShow: false,
			title:'您确定从收藏夹删除吗?',
			clickTitle:'管理',
			isClick: true
		};
	},
	components:{
		Dialog, NavigationBar
	},
	onLoad() {
		// status 商品状态(-1 已删除 0待审核 1审核中  2审核驳回  3已上架   4已下架  5 锁定 6 申请解锁")
		this.getCollectionData()
	},
	onShow() {
		
	},
	onReachBottom() {
		this.loadBottom()
	},
	methods: {
		doClick(e){
			this.isEdit = !this.isEdit;
			let res = this.list.map((item) => {
				item.isEdit = this.isEdit;
				return item
			});
			this.list = res;
			this.clickTitle = this.isEdit ? '完成' : '管理';
			
		},
		doConfirm() {
			this.doDel()				
		},
		doCancel(){
			this.isShow = false
		},
		// 去详情
		goDetail(shopId, orderId) {
			uni.navigateTo({
				url:'/pages/order/goodsDetail/goodsDetail?shopId='+shopId + '&goodsId='+orderId
			})
		},
		// 全选
		doCheckAll() {
			this.ids = []
			if (this.hasAllCheck) {
				this.hasAllCheck = false
				this.list.forEach((item, index) => {
					item.isCheck = false
					this.ids = []
				})
			} else {
				this.hasAllCheck = true
				this.list.forEach((item, index) => {
					item.isCheck = true
					this.ids.push(this.list[index].goodsId)
				})
			}
		},
		// 判断是否全选
		hasAllChecked() {
			this.hasAllCheck = true
			this.countChecked = 0
			this.ids = []
			this.list.forEach((item, index) => {
				if (!item.isCheck) {
					this.hasAllCheck = false
				} else {
					this.countChecked++
					this.ids.push(this.list[index].goodsId)
				}
			})
			console.log(this.countChecked)
		},
		// 选择动作
		doListCheck(index) {
			this.list[index].isCheck = !this.list[index].isCheck
			this.hasAllChecked()
		},
		// 上拉加载更多
		loadBottom() {
			// this.$refs.loadmore.onBottomLoaded();
			setTimeout(() => {
				if (!this.allLoaded) {
					this.currentPage++;
					this.getCollectionData()
				}
			}, 500)
		},
		getCollectionData() {
			let data = {
				pageIndex: this.currentPage,
				pageSize: this.pageSize
			};
			getCollectGoodsList(data).then((res) => {
				if (res.code === '1000') {
					let total = 0;
					this.list = this.list.concat(res.data.records);
					this.total = total;
					this.isClick = this.list.length > 0;
					if (this.list.length >= res.data.total) {
						this.allLoaded = true;
						this.loadText = '数据已经加载完毕'
					}
				} else {
					T.tips(res.message || '获取收藏列表失败')
				}
			}).catch(err => {
				T.tips(err.message || '获取收藏列表失败');
			})
		},
		edit() {
			this.isEdit = !this.isEdit;
			let res = this.list.map((item) => {
				item.isEdit = this.isEdit;
				return item
			});
			this.list = res
		},
		preDel() {
			if (this.ids.length < 1) {
				return T.tips('请先选择商品!')
			}
			this.isShow = true
		},
		doDel() {
			this.isShow = false
			let goodsId = ''
			this.ids.forEach((item, index) => {
				goodsId += index === this.ids.length - 1 ? item : item + ','
			})
			let data = {
				goodsId
			}
			getCollectGoodsRemove(data).then(res => {
				if (res.code === '1000') {
					this.ids.forEach((item, index) => {
						this.list.forEach((it, i) => {
							if (item === it.goodsId) {
								this.list.splice(i, 1);
							}
						})
					});
					this.ids = []
					this.isClick = this.list.length > 0;
					// this.list = []
					// this.getCollectionData()
				} else {
					T.tips(res.message || '删除失败')
				}
			}).catch(err => {
				T.tips(err.message || '删除失败')
			})
		}
	}
}
